
import '../App.css';
import { Box,Text, Flex, Heading, IconButton, Spacer, VStack, useColorMode, useMediaQuery } from '@chakra-ui/react';
import {FaGithub, FaInstagram, FaLinkedin, FaMoon, FaSun} from "react-icons/fa"


import Header from '../NavbarComponents/Header';
import Social from '../NavbarComponents/Social';
import Profile from '../NavbarComponents/Profile';
import { DrawerExample } from '../NavbarComponents/Drawer';

const Navbar = () => {
    const { colorMode, toggleColorMode } = useColorMode()
    const isDark=colorMode==="dark"
    const [isNotForSmallScreen] = useMediaQuery("(min-width:600px)")
    return (
     <>
    
       <VStack m="0" p="0">
        <Flex  position={'-webkit-sticky'} overflow={'hidden'} p={5} bgGradient={"linear(to-r, cyan.400,blue.500,purple.600)"} direction={isNotForSmallScreen ? "row" : "column"} 
        w={{base:"900px",sm:"870px",md:"1000px",lg:"100%",xl:"100%","2xl":"100%"}}
       
        >
          <Heading alignItems={{base:"center",sm:"center",md:"none",lg:"none",xl:"none","2xl":"none"}} border="1px solid black"  size="md" fontStyle={'italic'} fontWeight={'semibold'} display={{base:"none",sm:"none",md:"none",lg:"none",xl:"contents","2xl":"contents"}}  color={'teal.100'}>iamSourabh</Heading>
          <Flex border="1px solid black" w={{base:"50%",sm:"70%",md:"50%",lg:"50%",xl:"50%","2xl":"50%"}} pl={{base:"",sm:"",md:"",lg:"20",xl:"","2xl":""}} ml={{base:"35%",sm:"20%",md:"20",lg:"20",xl:"","2xl":""}} mr={{base:"-5%",sm:"2",md:"",lg:"",xl:"","2xl":""}} color={isDark && "white"} alignSelf={'center'}>
            <Text>Home</Text>
            <Spacer></Spacer>
            <Text>About</Text>
            <Spacer></Spacer>
            <Text>Skills</Text>
            <Spacer></Spacer>
            <Text>Projects</Text>
            <Spacer></Spacer>
            <Text>Statistics</Text>
          </Flex>
         <Spacer></Spacer>
         <Box ml="0" display={{base:"none",sm:"none",md:"block",lg:"block",xl:"block","2xl":"block"}}  w={{base:"80%",sm:"50%",md:"40%",lg:"35%",xl:"30%","2xl":"30%"}} border="1px solid black" pl={{base:"30%",sm:"3%",md:"1%",lg:"5%",xl:"8%","2xl":"none"}}>
        <IconButton ml={6}  icon={<FaLinkedin/>} isRound={true} onClick={toggleColorMode}></IconButton>
        <IconButton ml={6}  icon={<FaInstagram/>} isRound={true} onClick={toggleColorMode}></IconButton>
        <IconButton ml={6}  icon={<FaGithub/>} isRound={true} onClick={toggleColorMode}></IconButton>
        <IconButton ml={6}  icon={isDark?<FaSun/>:<FaMoon/>} isRound={true} onClick={toggleColorMode}></IconButton>
        </Box>
  
   draawer
            
  
           <DrawerExample/>
  
  
  
        </Flex>
        <Header/>
        <Social/>
        <Profile/> 
       </VStack>   
      </>
    );
  }

export default Navbar