import { Box, Text, Flex, useMediaQuery, Icon } from '@chakra-ui/react'
import React from 'react'
import { DiAndroid } from "react-icons/di"

const Profile = () => {
  const [isNotForSmallScreen] = useMediaQuery("(min-width:600px)")
  return (
    <Flex direction={isNotForSmallScreen ? "row" : "column"} w="100%" maxWidth={{ base: "100vh", md: "130vh", lg: "130vh", xl: "130vh" }}>

      <Box alignSelf={'center'} px="32" py="16">
        <Text fontWeight='bold' fontSize="2xl">Full  Stack Web Developer</Text>
        <Flex direction={isNotForSmallScreen ? "row" : "column"} mt={8}>
          <Flex rounded={'xl'} direction={'column'} mt={'4'} bg="blue.400" h="30vh" w="30vh" justify={'flex-end'}>
            <Icon color={'white'} p='4' as={DiAndroid} w='24' h='24'></Icon>
            <Text color={'white'} p='4' fontSize={'xl'} fontWeight={'semibold'}>
              Android Apps
            </Text>
          </Flex>
          <Flex rounded={'xl'} direction={'column'} mt={'4'} ml={isNotForSmallScreen?4:0} bg="gray.100" h="30vh" w="30vh" justify={'flex-end'} _hover={{bg:"green.400"}}>
            <Icon color={'black'} p='4' as={DiAndroid} w='24' h='24'></Icon>
            <Text color={'black'} p='4' fontSize={'xl'} fontWeight={'semibold'}>
             Flutter Apps
            </Text>
          </Flex>
          <Flex rounded={'xl'} direction={'column'} mt={'4'} ml={isNotForSmallScreen?4:0} bg="gray.100" h="30vh" w="30vh" justify={'flex-end'} _hover={{bg:"green.400"}}>
            <Icon color={'white'} p='4' as={DiAndroid} w='24' h='24'></Icon>
            <Text color={'white'} p='4' fontSize={'xl'} fontWeight={'semibold'}>
              Web Apps
            </Text>
          </Flex>
        </Flex>
      </Box>
    </Flex>

  )
}

export default Profile