import { Box, Text, Circle, Stack, useColorMode, useMediaQuery, Flex, Image, Button } from '@chakra-ui/react'
import React from 'react'

const Header = () => {
   const { colorMode } = useColorMode()
   const isDrak = colorMode === "dark"
   const [isNotForSmallScreen] = useMediaQuery("(min-width:600px)")
   return (
      <Stack >
         <Circle position={'absolute'} backgroundColor={'blue.100'} opacity={0.1} h={'300px'} w={'300px'} alignSelf={'flex-end'}></Circle>
         <Flex direction={isNotForSmallScreen ? "row" : "column"} spacing="200px" p={isNotForSmallScreen ? "32" : "0"} alignSelf="flex-start">

            <Box mt={isNotForSmallScreen ? "0" : '16'} align="flex-start">
               <Text fontSize={'5xl'} fontWeight={'semibold'}>Hi, I am</Text>
               <Text fontSize={'7xl'} fontWeight={'bold'} bgGradient={"linear(to-r, cyan.400,blue.500,purple.600)"} bgClip={'text'}>Sourabh Patidar</Text>
               <Text color={isDrak ? "gray.200" : "gray.500"}>Full Stack Web Developer</Text>
               <Button mt={8} colorScheme='blue' onClick={()=>window.open("https://drive.google.com/file/d/19KTxx4iOLBOozJerHMoTAHFldPlETYpd/view")}>Hire Me</Button>
            </Box>
          <Image alignSelf={'center'} mt={isNotForSmallScreen?"0":"12"} mb={isNotForSmallScreen?"0":"12"} borderRadius={'full'} backgroundColor={'transparent'} boxShadow={'lg'} boxSize={'300px'} src="https://avatars.githubusercontent.com/u/112754573?v=4"></Image>

         </Flex>
      </Stack>
   )
}

export default Header