import React from 'react'
import {
    Drawer,
    DrawerBody,
    DrawerFooter,
    DrawerHeader,
    DrawerOverlay,
    DrawerContent,
    DrawerCloseButton,
    useDisclosure,
    Button,
    Box,
    IconButton,
    useColorMode
  } from '@chakra-ui/react'

  import { HamburgerIcon } from '@chakra-ui/icons'
import { FaGithub, FaInstagram, FaLinkedin, FaMoon, FaSun } from 'react-icons/fa'

export function DrawerExample() {
    const { colorMode, toggleColorMode } = useColorMode()
    const isDark=colorMode==="dark"
    const { isOpen, onOpen, onClose } = useDisclosure()
    const btnRef = React.useRef()
  
    return (
      <>
        <Button ref={btnRef} colorScheme='white' onClick={onOpen}>
          <HamburgerIcon fontSize={"30px"} display={{base:"block",sm:"block",md:"none",lg:"none",xl:"none","2xl":"none"}}  color={'white'}/>
        </Button>
        <Drawer
          isOpen={isOpen}
        // isOpen={{base:isOpen,sm:isOpen}}
          placement='right'
          onClose={onClose}
          finalFocusRef={btnRef}
        >
          <DrawerOverlay />
          <DrawerContent>
            <DrawerCloseButton />
            <DrawerHeader>Create your account</DrawerHeader>
  
            <DrawerBody>
              {/* <Input placeholder='Type here...' /> */}
              <Box ml="0" display={{base:"block",sm:"block",md:"none",lg:"none",xl:"none","2xl":"none"}}  >
      <IconButton ml={6}  icon={<FaLinkedin/>} isRound={true} onClick={toggleColorMode}></IconButton>
      <IconButton ml={6}  icon={<FaInstagram/>} isRound={true} onClick={toggleColorMode}></IconButton>
      <IconButton ml={6}  icon={<FaGithub/>} isRound={true} onClick={toggleColorMode}></IconButton>
      <IconButton ml={6}  icon={isDark?<FaSun/>:<FaMoon/>} isRound={true} onClick={toggleColorMode}></IconButton>
      </Box>
            </DrawerBody>
  
            <DrawerFooter>
              <Button variant='outline' mr={3} onClick={onClose}>
                Cancel
              </Button>
              <Button colorScheme='blue'>Save</Button>
            </DrawerFooter>
          </DrawerContent>
        </Drawer>
      </>
    )
  }